@use '~sass-rem' as srem;

@import '@styles/variables';

.root {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 0;
  text-decoration: none;
  appearance: none;
  cursor: pointer;
  padding: srem.convert(13px) srem.convert(20px) srem.convert(7px);
  color: $white;
  transform: skewX(-5deg);
  background-color: $black;
  font-family: $displayFont;
  font-size: srem.convert(16px);
  font-weight: 900;
  line-height: 1;

  span {
    transform: skewX(5deg);
  }

  &:disabled {
    opacity: 0.25;
  }
}

.containedButton {
  display: flex;
  align-items: center;
  border-radius: srem.convert(8px);
  background: $igorLightGrey;
  color: $white;
  padding: srem.convert(10px) srem.convert(20px);

  &.clickable {
    background: $igorBlack;
  }
  &.hidden {
    visibility: hidden;
  }

  &.selected {
    background: $igorBlack;
  }
  &.green {
    background: $igorGreen;
    color: $igorBlack;
  }
  &.grey,
  &.gray {
    background: $igorDarkWhite;
    color: $igorLightBlack;
  }
  &.red {
    background-color: $red;
  }

  &.darkGrey {
    background-color: $darkGrey;
  }

  &.yellow {
    background-color: $yellow;
  }
  &.white {
    background: white;
    color: black;
  }

  &.hollow {
    background: transparent;
    color: $igorBlack;
    border: 1px solid $igorBlack;
  }

  &.wide {
    width: 100%;
  }
}

.capsuleNavButton {
  border-radius: 9999px;
  border: 1px solid $igorMidLightGrey;
  color: $igorMidLightGrey;
  padding: srem.convert(6px) srem.convert(20px);
  display: inline;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;

  &.selected {
    color: $igorBlack;
    border-color: $igorBlack;
  }

  &.finished {
    background: $igorLightGreen;
  }
}

.containedNavButton {
  border-radius: 4px;
  border: 1px solid $igorMidLightGrey;
  color: $igorMidLightGrey;
  padding: srem.convert(6px) srem.convert(20px);
  display: inline;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;

  &.selected {
    color: $igorBlack;
    border-color: $igorBlack;
  }

  &.finished {
    background: $igorLightGreen;
  }
}

.stepsNavButtons {
  background: $igorLightGrey;
  color: $white;
  padding: 10px 20px;
  border-radius: 8px;
  width: 133px;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  cursor: unset;

  &.clickable {
    background: $igorBlack;
    cursor: pointer;
  }

  &.visible {
    visibility: visible;
  }

  &__next {
    grid-area: right;
  }
  &__prev {
    grid-area: left;
  }
  &__cancel {
    grid-area: left;
  }
}

.togglePillButton {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: $igorLightGreen;
  padding: 8px 24px;
  border-radius: 4px;

  color: $igorLightBlack;

  &.on {
    background: $igorLightRed;
  }
}
